import {
	entry,
	isDevelopment,
	socket
} from '../prepare.build-tools.config'

export default () => {
	if (isDevelopment) {
		return [
			// http://gaearon.github.io/react-hot-loader/getstarted/
			`webpack-dev-server/client?${socket}`,
			'webpack/hot/dev-server',
			'react-hot-loader/patch',
			entry
		]
	}

	return entry
}